import { createLang } from '../utils';

const languageMap = {
    Global: {
        default: 'Global',
        'zh-cn': ''
    },
    Project: {
        default: 'Project',
        'zh-cn': ''
    }
};

const lang: Record<keyof typeof languageMap, () => string> = {} as any;

createLang(lang as any, languageMap, 'en');

export function reCreateLang(local: string) {
    createLang(lang, languageMap, local);
}

export default lang;
